<template>
<div class="text-xxl-center fw-bold">
    <span>Cho mng bn n Website gim st h thng ca Ban iu hnh h thng IFC.</span>
    <br>
    <span v-if="isUserAcc" class="text-red">Bn khng c quyn  xem mt s tnh nng quan trng ca website. Vui lng lin h qun tr vin.</span>
    <span v-else>Hy chn chc nng bn tri</span>
</div>
</template>

<script>
import { lowerCase } from 'lodash'
import { mapGetters } from 'vuex'
import SideNav from './SideNav.vue'
export default {
    name: "Default",
    components: {
        SideNav
    },
    data() {
      return {
        user: null
      }
    },
    computed: {
      ...mapGetters(["getUser"]),
      isUserAcc() {
        this.user = this.getUser
        if (lowerCase(this.user.role) == "user") {
          return true
        }
        return false
      }
    }
}
</script>

