<template>
<!-- Modal -->
<div ref="modal" class="modal fade" id="deleteRoleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog">
        <div class="modal-content">
            <div class="modal-header">
                <h5 class="modal-title" id="exampleModalLabel">Role Removing</h5>
            </div>
            <div class="modal-body">
                <span>Vui lng nhp mt khu xc nhn</span>
                <div class="form-floating mb-3">
                    <input @input="clearAlert" name="password" class="form-control" id="inputPasswordModal" type="pasword" v-model="password" />
                    <label for="inputRolenameModal">password</label>
                </div>
            </div>

            <div v-if="isMessage" :class='"alert mx-3 alert-" + type' role="alert">
                {{ message }}
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Khng</button>
                <button v-if="isUpdated" type="button" class="btn btn-primary" @click="deleteRole" :disabled="isDisabled">Xa Role</button>
                <button v-else class="btn btn-primary" type="button" disabled>
                    <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                    Ch t...
                </button>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import {
    mapActions
} from 'vuex'
import dbRequest from '../../apis/dbRequest'

export default {
    name: "ModalDeleteRole",
    data() {
        return {
            Role: {},
            isUpdated: true,
            isMessage: false,
            message: "",
            type: "",
            password: "",
            isDisabled: false
        }
    },
    props: ["modal"],
    methods: {
        ...mapActions(["deleteRoleManager"]),
        clearAlert() {
            this.isMessage = false
        },
        deleteRole() {
            const id = this.modal.data.id
            // get password from input
            const password = this.password
            if (password === process.env.VUE_APP_CONFIR_PWD_DEL_USER ?? "") {
                // xoa user neu mat khau dung
                dbRequest.deleteRole(id)
                    .then(() => {
                        this.isMessage = true
                        this.type = "success"
                        this.message = "Xa Role thnh cng"
                        // delete roles in managerStore
                        this.deleteRoleManager(id)
                        this.isDisabled = true
                    })
                    .catch(() => {
                        this.isMessage = true
                        this.type = "danger"
                        this.message = "Tht bi trong qu trnh tng tc vi DB"
                    })
            } else {
                this.isMessage = true
                this.type = "danger"
                this.message = "Mt khu khng ng"
            }
        },
    },
}
</script>

